<template>
  <!-- $route: url에 담긴 정보 -->
  <!-- {{$route.params.id}} -->
  <!-- Blog Post Detail -->
  <div class="container mt-4">
    <div class="row">
      <!-- Blog Post Content Column -->
      <div>
        <!-- Title -->
        <!-- $route: 현재 경로 / $router: 페이지 이동 관련 기능 -->
        <h1 class="mt-4" @click="$router.push('/detail/'+$route.params.id)">{{blogData[$route.params.id].title}}</h1>
        <!-- Author -->
        <p class="lead" v-if="$route.path !== '/detail/'+$route.params.id+'/author'">
          by <router-link :to="$route.params.id+'/author'">{{blogData[$route.params.id].author}}</router-link>
        </p>
        <router-view></router-view>
        <hr>
        <!-- Date/Time -->
        <p>{{blogData[$route.params.id].data}}</p>
        <!-- Preview Image -->
        <img class="img-fluid rounded mb-4" src="https://via.placeholder.com/750x300" alt="Post image">
        <!-- Post Content -->
        <p>{{blogData[$route.params.id].content}}</p>
        <hr>
        <br>

        <!-- Comments Section -->
        <div class="card my-4">
          <h5 class="card-header">Leave a Comment:</h5>
          <div class="card-body">
            <form>
              <div class="form-group">
                <textarea class="form-control" rows="3" placeholder="Write your comment here..."></textarea>
              </div>
              <button type="submit" class="btn btn-primary">Submit</button>
            </form>
          </div>
        </div>

        <!-- Comments -->
        <div class="media mb-4">
          <img class="d-flex mr-3 rounded-circle" src="https://via.placeholder.com/50" alt="User avatar">
          <div class="media-body">
            <h5 class="mt-0">Commenter Name</h5>
            This is a comment. Lorem ipsum dolor sit amet, consectetur adipiscing elit.
          </div>
        </div>

        <div class="media mb-4">
          <img class="d-flex mr-3 rounded-circle" src="https://via.placeholder.com/50" alt="User avatar">
          <div class="media-body">
            <h5 class="mt-0">Commenter Name</h5>
            This is another comment. Praesent eu dui. Cras nec ante. Pellentesque a nulla.
          </div>
        </div>
      </div>      
    </div>
  </div>
</template>

<script>
export default {
    name: 'DeatilCom',
    props: {
      blogData: Array
    }
}
</script>

